import react, { useState } from "react";
import s from "./Main.module.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import img2 from "../assets/images/img1.webp";
import img1 from "../assets/images/img2.jpg";
import img3 from "../assets/images/img3.webp";

import { TransitionGroup, CSSTransition } from "react-transition-group";
import { SidebarAccessories } from "../Header/Sidebar/SidebarAccessories/SidebarAccessories";


const images = [img1, img2, img3];

export const Main = () => {
  const [current, setCurrent] = useState(0);
  const length = images.length;

  const nextSlide = () => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const prevSlide = () => {
    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  return (
    <div className={s.main}>
      <div className={s.slider}>
        <div className={s.button_container} style={{ margin: "0 auto" }}>
          <button className={s.left_arrow} onClick={prevSlide}>
            <svg
              width="55"
              height="55"
              viewBox="0 0 14 14"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill="none"
                stroke="#000000"
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M10.15.5L4 6.65a.48.48 0 0 0 0 .7l6.15 6.15"
              />
            </svg>
          </button>
          <button className={s.right_arrow} onClick={nextSlide}>
            <svg
              width="55"
              height="55"
              viewBox="0 0 14 14"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill="none"
                stroke="#000000"
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M3.85.5L10 6.65a.48.48 0 0 1 0 .7L3.85 13.5"
              />
            </svg>
          </button>
        </div>
        <TransitionGroup>
          {images.map((img, index) => (
            <CSSTransition key={index} timeout={1000} classNames="fade">
              <div className={index === current ? "slide active" : "slide"}>
                {index === current && (
                  <img src={img} alt="" className={s.image} />
                )}
              </div>
            </CSSTransition>
          ))}
        </TransitionGroup>
      </div>
    </div>
  );
};
